/* Layout
-------------------------------------------------------------*/
body {
	width: 100vw;
	overflow-x: hidden;
}

.body-color {
	background-color: #f0f0f0; 
}

.container {
	width: 1200px;
	margin: 0 auto;
	position: relative;

	@media(max-width: 1260px) {
		width: 90%;
	}
}

section, footer {
	overflow: hidden;
}



/* Menu
-------------------------------------------------------------*/

.menu-active {
	overflow: hidden;
	.menu {
    	transform: translateX(0);
   	}

   	.dot {
		opacity: 1;
		fill: white;
		-ms-transform: scale(1);
	    -webkit-transform: scale(1);
	    transform: scale(1);
	    transform-origin: 50% 50%;
	}

   	.burger-dots {
   		fill: white;
   	}

}

.menu {
	overflow: hidden;
    background-color: #0e141a;
    position: fixed;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    z-index: 2;
    display: block;
    transform: translateX(100%);
    transition: all .3s cubic-bezier(.215,.61,.356,1);
}

.menu-nav {
	padding-top: calc(50vh - 215px);
}



/* Header
-------------------------------------------------------------*/

.logo {
	display: inline-block;
}

.header {
	height: 130px;
	top: 0;
    padding-right: 40px;
    padding-left: 40px;
    padding-top: 30px;
    background-color: $mainColor;

    @media(max-width: 580px) {
		padding-right: 25px;
	    padding-left: 25px;
	    padding-top: 25px;
	}

    &:after {
	    content: " ";
	    position: absolute;
	    top: 130px;
	    left: 0;
	    right: 0;
	    width: 100%;
	    height: 335px;
	    background: $mainColor;
	    z-index: -1;

	    @media(max-width: 580px) {
			height: 190px;
		}
	}
}

.header-bg {
	&:after {
	    height: 420px;
	}
}

.header--color-none {
	background-color: #fff;

	&:after {
		background-color: #fff;
	}
}

.header--color {
	background-color: $secondColor;

	&:after {
		background-color: $secondColor;
	}
}

.header--color2 {
	background-color: #0e141a;

	&:after {
		background-color: #0e141a;
	}
}


.burger {
	width: 25px;
	height: 25px;
	position: fixed;
	top: 40px;
	right: 40px;
	z-index: 3;
	cursor: pointer;

	@media(max-width: 580px) {
		top: 35px;
		right: 25px;
	}

	.burger-dots {
		transition: 0.3s;
		transform-origin: 50% 50%;
	}

	.dot {
		opacity: 0;
		-ms-transform: scale(0);
	    -webkit-transform: scale(0);
	    transform: scale(0);
	    transform-origin: 50% 50%;
	    transition: 0.3s;
	}

	&:hover{
		.burger-dots {
			opacity: 0;
			-ms-transform: scale(0);
		    -webkit-transform: scale(0);
		    transform: scale(0);
		    transform-origin: 50% 50%;
		}

		.dot {
		opacity: 1;
		-ms-transform: scale(1);
	    -webkit-transform: scale(1);
	    transform: scale(1);
	    transform-origin: 50% 50%;
	}
	}
}

.fill {
	fill: #fff;
}



/* Footer
-------------------------------------------------------------*/

.footer {
	margin-top: 12.8rem;
	display: flex;
	padding-bottom: 4.4rem;


	@media(max-width: 670px) {
		margin-top: 100px;
	}

	@media(max-width: 1147px) {
		display: block;
	}
}

.box {
	@media(max-width: 1147px) {
		display: inline-block;
		vertical-align: top;
	}
}

.box__large {
	width: 280px;

	@media(max-width: 670px) {
		display: block;
		width: 280px;
		margin: 0;
		padding-top: 60px;

		&:first-child {
			padding-top: 0;
		}
	}

	@media(max-width: 1147px) {
		width: calc(50% - 17px);
	}
}

.box__large--left {
	margin-left: 28px;

	@media(max-width: 670px) {
		margin-left: 0;
	}
}

.box__small {
	@media(max-width: 670px) {
		display: block;
		width: 280px;
		margin: 0;
		padding-top: 60px;
	}

	@media(min-width: 671px) {
		width: calc(50% - 17px);
		margin-left: 0;
		padding-top: 60px;

		&:last-child {
			margin-left: 28px;
		}
	}

	@media(min-width: 1147px) {
		width: 180px;
		margin-left: 130px;
		padding-top: 0;

		&:last-child {
			margin-left: 130px;
		}
	}
}

.box ul {
	list-style: none;
	margin: 0;
	padding: 0;
}



/* Listes
-------------------------------------------------------------*/

ul {
	list-style: none;
	padding: 0;
	margin: 0;
}



/* Classes
-------------------------------------------------------------*/

.fr {
	float: right;
}

.space-bottom {
	margin-bottom: 140px;
}

.big-img {
	width: 100vw;

	@media(min-width: 690px) {
		width: 90vw;
		display: block;
		margin: 0 auto;
	}

	@media(min-width: 1200px) {
		max-width: 1200px;
	}
}

.col-center {
	max-width: 588px;
	margin: 0 auto;
	padding-top: 30px;

}

.col-center--padding {
		padding-top: 100px;
	}

.img-row {
	position: relative;
	margin: 140px 0 120px 0;

	@media(max-width: 670px) {
		margin: 100px 0 80px 0;
	}
}

.img-row__el--big {
	@media(max-width: 1160px) {
		width: 100%;
	}
}

.img-row__el--small {
	max-width: 583px;
	position: absolute;
	top: 57px;
	right: 0;

	@media(max-width: 1160px) {
		display: none;
	}
}



/* Page expo
-------------------------------------------------------------*/

.img-expo {
	width: 100%;
	margin-bottom: 80px;

	@media(max-width: 670px) {
		margin-bottom: 60px;
	}
}

.infos__el {
	padding-bottom: 40px;

	&:last-child {
		padding-bottom: 0;
	}
}


.description__el {
	display: inline-block;

	@media(max-width: 1090px) {
		display: block;
		margin: 0 auto;
	}
}

.description__el--infos {
	max-width: 383px;
	vertical-align: top;

	@media(max-width: 1090px) {
		max-width: 485px;
		display: block;
		margin: 0 auto;
	}
	
	@media(min-width: 1091px) {
		padding-left: 103px;
	}
}

.description__el--resume {
	max-width: 485px;

	@media(max-width: 1090px) {
		padding-top: 80px;
		display: block;
		margin: 0 auto;
	}

	@media(min-width: 1091px) {
		padding-left: 130px;
	}
}



/* Programme
-------------------------------------------------------------*/

.programme {
	.col-center {
		padding: 0 0 120px 0;

		@media(max-width: 670px) {
			padding: 0 0 80px 0;
		}
	}
}

.event__el {
	display: inline-block;
	max-width: 100%;
	margin: 0 0 40px 0;



	@media(min-width: 580px) {
		max-width: calc(50% - 12px);
		margin: 0 19px 55px 0;
	}

	@media(max-width: 1260px) {

		&:nth-child(2n) {
	    	margin: 0 0 55px 0;
	    }

    	&:last-child {
			margin: 0;
		}
    }

	@media(min-width: 1260px) {
		max-width: 381px;
		margin: 0 22px 55px 0;


		&:nth-child(3), &:nth-child(6) {
	    	margin: 0 0 55px 0;
	    }
	}
}

.event__img {
	max-width: 100%;
}

.event__info h2 {
	padding-top: 16px;
	margin-bottom: 5px;
}

.nav-event {
	margin: 120px 0 0;
}

.nav-event__el {
	display: inline-block;
}

.nav-event__el--next {
	text-align: right;
	float: right;
}
