body {
	font-family: $baseFont;
	font-size: 18px;
	color: $fontColor;
	line-height: 1.5;
}

.aktiv {
	font-family: $secondFont;
	font-weight: 700;
	color: $fontColor2;
}

.aktiv-white {
	font-family: $secondFont;
	font-weight: 700;
	color: #fff;
}

p {
	margin: 0;
}

.space-top {
	padding-top: 30px;
}

.font-color {
	color: $colorOrange;
}


/* Link & btn
-------------------------------------------------------------*/
a {
	text-decoration: none;
	color: inherit;
	transition: 0.3s;
}
a:hover {
	color: $colorOrange;
}

.button{
  background: $colorOrange;
  color:#fff;
  border:none;
  border-radius: 3px;
  position:relative;
  font-size:0.9em;
  font-weight: 700;
  padding:10px 31px;
  width: 218px;
  cursor:pointer;
  transition:800ms ease all;
  outline:none;
  margin-top: 40px;
}
.button:hover{
  background: transparent;
  color: $colorOrange;
}
.button:before,.button:after{
  content:'';
  position:absolute;
  top:0;
  right:0;
  height:2px;
  width:0;
  background: $colorOrange;
  transition:400ms ease all;
}
.button:after{
  right:inherit;
  top:inherit;
  left:0;
  bottom:0;
}
.button:hover:before,.button:hover:after{
  width:100%;
  transition:800ms ease all;
}

.button-menu {
	max-width: 215px;
	font-size: 18px;
	color: #2b2b2b;
	margin-top: 60px;
	margin-bottom: 30px;
}

.button-appli {
	font-family: $secondFont;
	font-weight: 700;
	font-size: 1em;
	color: #fff;
	display: block;
	margin: 100px auto;
}

/* Title
-------------------------------------------------------------*/

h1, h2 {
	font-family: $secondFont;
	font-weight: 700;
	color: $fontColor2;
}

h1 {
	font-size: 1.999em;
	margin: 0 0 17px 0;
}

h2 {
	font-size: 1.414em;
	margin: 0 0 22px 0;
}

h3 {
	font-family: $secondFont;
	font-weight: 700;
	font-size: 19px;
	text-transform: uppercase;
	color: $colorOrange;
	margin: 0 0 14px 0;
}


/* Menu
-------------------------------------------------------------*/

.menu-nav {
	position: relative;
	font-family: $secondFont;
	font-weight: 700;
	font-size: 1.999em;
	color: #fff;

	.menu-nav__el--social {
		font-weight: 400;
		font-size: 14px;
		text-transform: uppercase;
		padding-bottom: 3px;
	}
}

.credit {
	font-family: $secondFont;
	font-weight: 400;
	color: #fff;
	font-size: 15px;
	position: fixed;
	right: 40px;
	bottom: 20px;

	@media(max-width: 580px) {
		right: 20px;
		bottom: 18px;
		opacity: 0.6;
	}
}

.credit ul {
	text-align: right;
}

.credit li {
	padding-top: 6px;
}


/* Footer
-------------------------------------------------------------*/

.footer {
	font-family: $secondFont;
	font-weight: 500;
	font-size: 0.8em;	
}

.footer--nav {
	font-weight: 700;
	font-size: 1.414em;
	color: $fontColor2;
}

.footer--title {
	font-weight: 700;
	text-transform: uppercase;
	color: $fontColor2;
	margin-bottom: 12px;
}


/* Page expo
-------------------------------------------------------------*/

.infos ul {
	font-size: 0.9em;
}

.infos__el p {
	margin: 0;
}

.nav-event h3 {
	font-size: 17px;
	margin: 0 0 6px 0;
}

.nav-event h2 {
	margin: 0 0 12px 0;

	@media(max-width: 670px) {
		display: none;
	}
}